import * as React from "react";
import {Link} from 'gatsby';

const CShirtsPreview = ({id, url, imgpath, collection} : {id:string, url:string, imgpath:string, collection:string}) => {
    return (        
        <div className="img-box">  
            <Link to={url + id + '/'}>
                <img src={imgpath + id + ".png"} alt={collection + ' #' + id} />
                <div className="cshirt-info">
                    <p className="chsirt-number">#{id}</p>
                </div>
            </Link>          
        </div>
    );
};

export default CShirtsPreview;